.homepage{
    display: grid;
    flex-direction: row;
    align-items: center;
    padding: 20px 80px;
}

.directory-menu{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

}
    .menu-item{
        min-width: 30%;
        height: 240px;
        flex: 1 1 auto;
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid rgb(45, 44, 44);
         margin: 0 7.5px 15px;

        &:first-child{
            margin-right: 7.5px;
        }

        &:last-child{
            margin-right: 7.5px;
        }

    

        .content{
            height: 90px;
            padding: 0 25px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            border: 1px solid rgb(45, 44, 44);

            .tittle{
                font-weight: bold;
                margin-bottom: 0px;
                font-size: 22px;
                color: #4a4a4a;
            }
            .subtitle{
                font-weight: lighter;
                font-size: 16px;
                margin-bottom: 5px;
            }
         }
    }